body {
  /*font-family: "Open Sans", Arial, sans-serif;*/
  font-family: "Lucida Console", "Courier New", monospace;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.8;
  color: #eae4ff;
  background: #8f50b9;
  margin: 0;
  /*text-align: center;*/
  justify-content: center;

}

.highlight {
  background-color: #4c417a;
  opacity: 0.7;
  padding: 1px;
  color: rgb(223, 226, 241);
}

.skill-highlight {
  background-color: rgb(59, 216, 59);
  opacity: 0.6;
  padding-left: 1%;
  padding-right: 1%;
  color: #000;
  justify-content: center;
}

a {
  -webkit-transition: .3s all ease;
  -o-transition: .3s all ease;
  transition: .3s all ease;
  text-decoration: none;
  color: #07272b;
  font-weight: bolder;
}

a:hover {
  text-decoration: underline;
  color: #0e353a;
}

h1 {
  color: rgb(255, 255, 255);
  font-weight: bolder;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .15em solid orange; /* The typwriter cursor */
  white-space:initial; 
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;

}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange; }
}

h2,
h3,
h4,
h5 {
  color: #000;
  font-family: "Playfair Display", times, serif;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
}

header .navbar-brand {
  text-transform: uppercase;
  letter-spacing: .2em;
  font-weight: 400;
}

header .navbar-brand.absolute {
  position: absolute;
}

@media (max-width: 991px) {
  header .navbar-brand.absolute {
    position: relative;
  }
}

header .navbar-brand span {
  color: #F73859;
}

header .navbar {
  padding-top: 0;
  padding-bottom: 0;
  background: none !important;
  position: relative;
}

@media (max-width: 767px) {
  header .navbar {
    background: black !important;
    padding-top: .5rem;
    padding-bottom: .5rem;
  }
}

header .navbar .nav-link {
  padding: 1.7rem 1rem;
  outline: none !important;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: .2em;
}

@media (max-width: 767px) {
  header .navbar .nav-link {
    padding: .5rem 0rem;
  }
}

header .navbar .nav-link.active {
  color: #fff;
}

header .navbar .dropdown-menu {
  font-size: 14px;
  border-radius: 0px;
  border: none;
  -webkit-box-shadow: 0 2px 30px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0 2px 30px 0px rgba(0, 0, 0, 0.2);
  min-width: 13em;
  margin-top: -10px;
}

header .navbar .dropdown-menu:before {
  bottom: 100%;
  left: 10%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: #fff;
  border-width: 7px;
}

@media (max-width: 991px) {
  header .navbar .dropdown-menu:before {
    display: none;
  }
}

header .navbar .dropdown-menu .dropdown-item:hover {
  background: #F73859;
  color: #fff;
}

header .navbar .dropdown-menu .dropdown-item.active {
  background: #F73859;
  color: #fff;
}

header .navbar .dropdown-menu a {
  padding-top: 7px;
  padding-bottom: 7px;
}

header .navbar .cta-btn a {
  background: #F73859;
  color: #000 !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
  text-transform: uppercase;
  padding-top: 26px !important;
  padding-bottom: 26px !important;
  line-height: 1;
  -webkit-transition: .3s all ease;
  -o-transition: .3s all ease;
  transition: .3s all ease;
}

header .navbar .cta-btn a:hover {
  color: #fff !important;
  -webkit-box-shadow: 2px 0 30px -5px rgba(247, 56, 89, 0.9);
  box-shadow: 2px 0 30px -5px rgba(247, 56, 89, 0.9);
}

.site-wrap {
  -webkit-transition: .3s all ease;
  -o-transition: .3s all ease;
  transition: .3s all ease;
}

main,
aside {
  width: 100%;
}

@media (min-width: 576px) {

  main,
  aside {
    width: 50%;
  }
}

aside {
  background-position: 100% center;
  background-size: cover;
  height: 400px;
  position: relative;
}

@media (min-width: 576px) {
  aside {
    height: 100vh;
    position: fixed;
    right: 0;
    top: 0;
  }
}

main {
  float: left;
  padding: 7%;
  position: relative;
}


@media (min-width: 576px) {
  .home-button {
    display: block;
  }
}


.half>div {
  width: 50%;
  padding: 4% 5%;
}

.half>div.bg {
  background-size: cover;
  background-position: center center;
}


@media (max-width: 991px) {
  .half>div.bg {
    height: 350px;
  }
}

@media (max-width: 991px) {
  .half>div {
    width: 100%;
  }
}

.section {
  padding: 7em 0;
}

.section-subscribe {
  padding: 2em 0;
}

.heading {
  color: #000;
}

.heading.border-bottom {
  position: relative;
  padding-bottom: 30px;
}

.heading.border-bottom:before {
  bottom: 0;
  position: absolute;
  content: "";
  width: 50px;
  height: 2px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  background: #F73859;
}

.text-black {
  color: #000 !important;
}



.greetSpace {
  color: #fff;
  font-weight: bold;
  animation: blinker 1s linear infinite;
  margin-top: 15px;
  position: relative;
}

.live {
  animation: blinker 1s linear infinite;
}

.wishBtn {
  display: flex;
  justify-items: center;
  animation: blinker 1s linear infinite;
  margin-top: 23px;
  position: relative;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

@media only screen and (min-width: 610px) {
  .greetSpace {
    margin-left: 100px;
    margin-top: 25px;
    font-size: large;
  }

}

@media only screen and (max-width: 600px) {
  .greetSpace {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
}

@media only screen and (min-width: 610px) {
  .wishBtn {
    margin-left: 100px;
    margin-top: 25px;
    font-size: large;
  }

}

@media only screen and (max-width: 600px) {
  .wishBtn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    margin-left: 200px;
    margin-bottom: 10px;
  }
}



.contactButton {
  background-image: linear-gradient(90deg, #7b81ec, #3bd1d3);
  color: rgb(252, 252, 252);
  border: 2px solid #fff;
  border-radius: 50px;
  padding: 0.3rem 1.2rem;
  font: 18px "Margarine", sans-serif;
  outline: none;
  cursor: pointer;
  position: relative;
  transition: 0.2s ease-in-out;
  letter-spacing: 2px;
  margin-left: 10px;
  font-family: "Lucida Console", "Courier New", monospace;
  margin-top: 1%;
  margin-bottom: 1%;
}

.skillButton {
  background: chartreuse;
  color: rgb(61, 59, 59);
  border: 2px solid #fff;
  border-radius: 50px;
  padding: 0.3rem 1.2rem;
  margin-top: 1%;
  margin-bottom: 1%;
  font-weight: bolder;
  outline: none;
  cursor: none;
  position: relative;
  transition: 0.2s ease-in-out;
  letter-spacing: 2px;
  margin-left: 10px;
  font-family: "Lucida Console", "Courier New", monospace;
}

.emailButton {
  /*background-image: linear-gradient(90deg, #fa80d2, #fed757);*/
  background-image: linear-gradient(90deg, #525a69, #898dda);
  color: rgb(252, 252, 252);
  border: 2px solid rgba(255, 255, 255, 0.45);
  border-radius: 50px;
  padding: 0.3rem 1.2rem;
  font: 18px "Margarine", sans-serif;
  outline: none;
  cursor: pointer;
  position: relative;
  transition: 0.2s ease-in-out;
  letter-spacing: 2px;
  margin-left: 10px;
  text-decoration: none;
  font-family: "Lucida Console", "Courier New", monospace;
}

